###########################################
###  Extension to JQuery promise object

# some browsers don't seem to like subclassing A+ Promise yet,
# so I'm going with JQuery Deferred for backwards compatability now.

# jQuery Deferred is constructed instead of created like a normal object,
# so I have to modify it after construction

def timer_deferred(object, Promise):
    def __init__(self, executor):
        object.__init__(self)
        Promise.apply(self, [ executor ])
        
    